// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle
  },
  data() {
    return {
      name: '',
      description: '',
      price_per_cycle: 0,
      cycle_duration: 1,
      hidden: false,
      loading: false
    }
  },
  computed: {
    ...mapState({
      space: state => state.space.currentSpace
    })
  },
  methods: {
    addPlan() {
      const {
        name,
        description,
        price_per_cycle,
        cycle_duration,
        hidden,
        space
      } = this

      this.loading = !this.loading
      const payload = {
        name,
        description,
        invoice_period: cycle_duration,
        price: price_per_cycle,
        recurring: true,
        hidden
      }
      this.$plan
        .addPlan(payload)
        .then(({ data: { data } }) => {
          this.$bvToast.toast(`Plan ~${name}~ created successfully`, {
            title: 'Success',
            variant: 'success',
            solid: true
          })
          this.loading = !this.loading
          this.$router.push({
            name: 'space-plans'
          })
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
          this.loading = !this.loading
        })
    }
  }
}
